}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  color: #000000;
}

p {
  text-align: justify;
  font-size: 1.7rem;
  font-weight: 400;
}

.contenedor {
  width: 90%;
  max-width: 135rem;
  margin: 0 auto;
}

.contenedor-interior {
  width: 80%;
  max-width: 135rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 2.5rem 0;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-family: "Inter", sans-serif;
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  display: flex;
  justify-content: center;
  align-items: center;
}

.titulo-h1-pagina {
  font-size: 5rem;
  font-weight: 800;
  color: #231F57;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.1;
  margin-top: 4rem;
}

.alineacion-izquierda {
  text-align: left;
}

.contenedor-boton-atras {
  display: flex;
  align-items: center;
  margin-top: 3rem;
}

.boton-atras {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #231F57;
  font-weight: 900;
}

label {
  font-family: "Poppins", sans-serif;
  font-size: 1.7rem;
  font-weight: 500;
  display: block;
}

.campos {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem;
  margin-top: 4rem;
}

.inputs {
  width: 100%;
  padding: 0.5rem 0.5rem 0.5rem 0;
  border: none; /* Primero, eliminamos todos los bordes */
  border-bottom: 0.2rem solid #231F57; /* Luego, agregamos un borde solo en la parte inferior */
  outline: none;
}

textarea {
  width: 100%;
  height: 8rem;
  padding: 0.5rem 0.5rem 0.5rem 0;
  border: none; /* Primero, eliminamos todos los bordes */
  border-bottom: 0.2rem solid #231F57; /* Luego, agregamos un borde solo en la parte inferior */
  outline: none;
  resize: none;
}

.boton-formulario-azul {
  padding: 1.5rem 4rem;
  background-color: #231F57;
  border: none;
  color: #FFFFFF;
  border-radius: 0.5rem;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  float: right;
}

.boton-formulario-blanco {
  padding: 0.8rem;
  background-color: #FFFFFF;
  border: 0.1rem solid #231F57;
  color: #000000;
  border-radius: 0.5rem;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  text-align: center;
}

.card {
  background-color: #30BDBC;
  text-align: center;
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1.5rem;
  font-weight: 800;
  color: #231F57;
  font-size: 2.5rem;
  padding: 1.5rem 0;
  cursor: pointer;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.boton-blanco, .navegacion .boton-blanco {
  background-color: #FFFFFF;
  color: #231F57;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-azul, .boton-azul, .navegacion .boton-azul {
  background-color: #231F57;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-chatbot {
  background-color: #FFFFFF;
  color: #000000;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #231F57;
  font-size: 1.5rem;
}

.boton-rojo, .navegacion .boton-rojo {
  background-color: #FF3333;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 2.5rem;
  text-align: center;
  margin-top: separacion/2;
  border-radius: 5rem;
  border: 0.1rem solid #FF3333;
  font-size: 1.5rem;
}

.boton-perfil {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.3rem;
}

.footer {
  margin-top: 5rem;
  width: 98%;
  max-width: 140rem;
  margin-right: auto;
  margin-left: auto;
}
.footer p {
  font-weight: 400;
  font-size: 1.4rem;
  text-align: center;
}

.contenedor-footer {
  width: 100%;
  background-color: #231F57;
  display: grid;
  grid-template-columns: 1.3fr 1fr 1.5fr 1fr;
  column-gap: 2rem;
  border-radius: 3rem;
  margin-bottom: 1rem;
}
.contenedor-footer h6 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 900;
  margin-bottom: 2.5rem;
}
.contenedor-footer .navegacion-footer {
  display: flex;
  flex-direction: column;
  margin-bottom: 3rem;
}
.contenedor-footer .navegacion-footer a {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
}
.contenedor-footer .navegacion-footer a:first-child {
  margin-top: 0;
}
.contenedor-footer ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-top: 0;
}
.contenedor-footer li {
  color: #FFFFFF;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1.3rem;
  display: flex;
  gap: 1rem;
}
.contenedor-footer li:first-child {
  margin-top: 0;
}
.contenedor-footer .footer-1 {
  background-color: #FFFFFF;
  margin: 2.5rem 12rem 2.5rem 3rem;
  border-radius: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1rem;
}
.contenedor-footer .footer-1 h5 {
  font-family: "Inter", sans-serif;
  color: #30BDBC;
  text-transform: uppercase;
  font-size: 2rem;
  font-weight: 900;
  margin-bottom: 1rem;
  text-align: center;
  margin-top: 0.2rem;
  line-height: 2.5rem;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}
.contenedor-footer .footer-1 img {
  width: 80%;
}
.contenedor-footer .footer-2, .contenedor-footer .footer-3, .contenedor-footer .footer-4 {
  padding-left: 3rem;
}

.contenido-eslogan {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
}
.contenido-eslogan h1 {
  color: #231F57;
  font-weight: 800;
  text-transform: uppercase;
  font-size: 5.9rem;
  text-align: left;
  line-height: 1.1;
  max-width: 70rem;
  margin-left: 2rem;
}
.contenido-eslogan .eslogan {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  background-image: url("../../build/img/Vector1.svg");
  background-size: contain; /* Ajusta la imagen para cubrir todo el cuadrado */
  background-repeat: no-repeat;
  background-position: center;
  background-position-y: 45%; /* Mover un poco hacia arriba */
  background-position-x: 0%;
  background-size: 80%;
}
.contenido-eslogan .eslogan_img {
  padding: 1rem 0 1rem 4rem;
}
.contenido-eslogan .eslogan_img img {
  height: 55rem;
  border-radius: 4rem;
}

.barra {
  display: flex;
  justify-content: space-between;
  padding-top: 1rem;
}
.barra .lado-izq {
  display: flex;
}
.barra .lado-izq img {
  height: 6rem;
  width: 6rem;
}
.barra .lado-izq .titulo {
  max-width: 20rem;
  color: #231F57;
  text-decoration: none;
  line-height: 1.2;
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 0.8rem;
}
.barra .lado-izq .titulo .segunda_parte {
  color: #30BDBC;
}

.navegacion {
  display: flex;
  align-items: center;
}
.navegacion a {
  color: #000000;
  font-size: 1.5rem;
  margin-right: 2rem;
  font-weight: 600;
}
.navegacion a:last-child {
  margin-right: 0;
}
.navegacion a.activo {
  color: #231F57;
  font-weight: 900;
}

.seccion-principal h2 {
  font-size: 3rem;
  font-weight: 800;
  text-align: center;
}
.seccion-principal .seccion {
  display: grid;
  grid-template-columns: 0.7fr 1fr;
  gap: 2rem;
}
.seccion-principal .seccion span {
  margin-top: 4rem;
  font-weight: 800;
}
.seccion-principal .seccion_img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.seccion-principal .seccion_img img {
  width: 60%;
  border-radius: 100%;
}

.testimoniales-contenedor-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.4rem;
}
.testimoniales-contenedor-cards .testimoniales-card {
  background-color: #30BDBC;
  padding: 4rem;
  border-radius: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.testimoniales-contenedor-cards .testimoniales-card img {
  width: 5.6rem;
  height: 5.6rem;
}
.testimoniales-contenedor-cards .testimoniales-card strong {
  text-transform: uppercase;
}

.estadisticas {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}
.estadisticas .estadisticas-img {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 12rem;
}
.estadisticas .estadisticas-img img {
  border-radius: 4rem;
}
.estadisticas .estadisticas-informacion {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 5rem;
  padding: 5rem;
}
.estadisticas .estadisticas-informacion h3 {
  font-weight: 900;
  font-size: 3.5rem;
  color: #231F57;
}
.estadisticas .estadisticas-informacion p {
  font-weight: 500;
  margin: 0;
}

.contenedor-chatbot {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}
.contenedor-chatbot button {
  color: #000000;
  font-size: 1.5rem;
  font-weight: 700;
  padding: 1.5rem;
  box-shadow: 2px 2px 2px 3px rgba(0, 0, 0, 0.2);
}
.contenedor-chatbot button:hover {
  background-color: #231F57;
  color: #FFFFFF;
  transition: all 0.5s;
}

.titulo-h3-pagina {
  margin-top: -4.5rem;
  font-family: "Poppins", sans-serif;
  color: #231F57;
  text-transform: uppercase;
  font-weight: 800;
}

.contenedor-table {
  background-color: #231F57;
}

#search {
  width: 50%;
  padding: 1rem;
  border: 1px solid #EAECF0;
  border-radius: 0.7rem;
  margin: 1rem;
  color: #000000;
}

table {
  width: 100%;
  border-collapse: collapse;
  background-color: white;
}

thead {
  background-color: #231F57;
  color: #30BDBC;
  border-bottom: 0.3rem solid #30BDBC;
}

th {
  padding: 1.2rem 1.5rem;
  font-size: 1.7rem;
}

td {
  padding: 1rem 1.5rem;
  background-color: #EAECF0;
}

th, td {
  text-align: left;
  font-family: "Poppins", sans-serif;
}

tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.edit, .delete, .verOrden {
  text-align: left;
  border: none;
  font-weight: 500;
  border-radius: 1rem;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
}

.edit, .verOrden {
  background-color: #30BDBC;
  color: #231F57;
  padding: 0.4rem 3rem;
}

.delete {
  background-color: #FF3333;
  color: #FFFFFF;
  padding: 0.4rem 2.3rem;
}

.articulo-servicios-izq {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 2rem;
  margin-top: 4rem;
}
.articulo-servicios-izq .articulo-servicios-titulo {
  color: #231F57;
  text-align: left;
  margin-bottom: 0;
  margin-top: 0;
}
.articulo-servicios-izq p {
  margin-top: 0;
}
.articulo-servicios-izq img {
  width: 80%;
  height: 100%;
  border-radius: 3rem;
}

.articulo-servicios-der {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 2rem;
  margin-top: 4rem;
}
.articulo-servicios-der .articulo-servicios-titulo {
  color: #231F57;
  text-align: left;
  margin-bottom: 0;
  margin-top: 0;
}
.articulo-servicios-der .articulo-servicios-titulo.titulo-vision {
  text-align: right;
}
.articulo-servicios-der p {
  margin-top: 0;
}
.articulo-servicios-der img {
  width: 80%;
  height: 100%;
  border-radius: 3rem;
}

.blog-articles {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3rem;
  margin-top: 5rem;
}
.blog-articles section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.blog-articles section h5 {
  margin-top: 2rem;
  background-color: #A6E8F6;
  display: inline-block;
  padding: 0.3rem 0.8rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.blog-articles section:nth-child(2) h5 {
  background-color: #FCBEDC;
}
.blog-articles section:nth-child(3) h5 {
  background-color: #D3F985;
}
.blog-articles section p {
  margin-top: 0;
}
.blog-articles section a {
  color: #231F57;
  font-weight: 900;
}
.blog-articles section div:nth-child(2) {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.blog-articles section img {
  border-radius: 2.5rem;
}

.subtitulo_blogs {
  color: #231F57;
  text-align: left;
  margin-bottom: 0;
  margin-top: 0;
}

.main-blogs {
  margin-top: 4rem;
}
.main-blogs p {
  margin: 0;
}

.margen-inferior {
  margin-bottom: 4rem;
}

.margen-superior {
  margin-top: 4rem;
}

.formulario-contacto {
  display: grid;
  grid-template-columns: 1.12fr 2fr;
  gap: 2rem;
  margin-top: 5rem;
